<template>
  <div>
    <!--  Header -->
    <Header :cartCount="cart.items.length" />
    
    <!--  -->
    <div class="container mt-5">
      <h2> </h2>

      <form @submit.prevent="submitOrder">
        <div class="form-group">
          <label></label>
          <input v-model="form.name" type="text" class="form-control" required>
        </div>
        <div class="form-group">
          <label></label>
          <input v-model="form.address" type="text" class="form-control" required>
        </div>
        <div class="form-group">
          <label></label>
          <input v-model="form.phone" type="text" class="form-control" required>
        </div>
        <div class="form-group">
          <label></label>
          <textarea v-model="form.note" class="form-control"></textarea>
        </div>

        <button class="btn btn-success mt-3" type="submit"></button>
      </form>
    </div>
  </div>
</template>

<script setup>
import Header from '@/components/Header.vue'   // 
import { reactive } from 'vue'
import { useCartStore } from '@/stores/cart'
import { useAuthStore } from '@/stores/auth' //  
import axios from 'axios'
const API_URL = process.env.VUE_APP_API
const cart = useCartStore()
const auth = useAuthStore() //  

const form = reactive({
  name: '',
  address: '',
  phone: '',
  note: ''
})

async function submitOrder() {
  try {
    if (!auth.user?.id) {
      alert(' ')
      return
    }

    // 1. 
    const payload = {
      user_id: auth.user.id, //   ID
      ...form,
      items: cart.items.map(item => ({
        product_id: item.id,
        quantity: item.quantity
      }))
    }

    const orderRes = await axios.post(`${API_URL}/api/orders`, payload)
    const order = orderRes.data
    console.log(':', order)
    // 2. 
    const ecpayRes = await axios.post(`${API_URL}/api/ecpay-pay`, {
      amount: order.amount, // 
      desc: '123',
      itemName: '456',
      orderNo: order.order_id
    })
    
    // 3. 
    cart.clearCart()

    // 4.  <div> 
    const formDiv = document.createElement('div')
    formDiv.innerHTML = ecpayRes.data
    document.body.appendChild(formDiv)
    formDiv.querySelector('form').submit()
  } catch (err) {
     if (err?.response?.status === 400 && err?.response?.data?.error === '') {
      alert(' ')
      return
    }
    alert(' ')
    console.error(err)

  //   alert(' ')
  //   cart.clearCart()
  // } catch (err) {
  //   alert(' ')
  //   console.error(err)
  // }
}}
</script>
