// src/stores/cart.js
import { defineStore } from 'pinia'
import { useAuthStore } from './auth'
const API_URL = process.env.VUE_APP_API
export const useCartStore = defineStore('cart', {
  state: () => ({
    items: [] //  { id, name, price, quantity }
  }),
  actions: {
    
   
    // 
    async clearCart() {
    const auth = useAuthStore()
    this.items = []
    if (auth.user?.id) {
      await fetch(`${API_URL}/api/cart/clear`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ user_id: auth.user.id })
      })
    }},

    // 
    async addItem(product) {
      const existing = this.items.find(p => p.id === product.id)
      const newQty = existing ? existing.quantity + 1 : 1
  // 1 
  const res = await fetch(`${API_URL}/api/products/${product.id}`)
  const data = await res.json()
  if (!res.ok || !data) {
    alert('')
    return
  }
  if (newQty > data.stock) {
    alert(` ${data.stock} `)
    return
  }
  // 2   
  if (existing) {
    existing.quantity = newQty
  } else {
    this.items.push({ ...product, quantity: newQty })
  }
  console.log('', JSON.stringify(this.items))
  // 3 

      const auth = useAuthStore()
      if (auth.user?.id) {
        await fetch(`${API_URL}/api/cart`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            user_id: auth.user.id,
            product_id: product.id,
            quantity: newQty //  
          })
        })
      }
    },

  // 
// stores/cart.js
async individualaddItem({ id, name, price, image, addQty, stock }) {
  if (!addQty || addQty <= 0) {
    alert(''); return
  }

  const existing = this.items.find(p => p.id === id)
  const currentQty = existing ? existing.quantity : 0
  const requestedTotal = currentQty + addQty

  // 
  if (typeof stock === 'number' && requestedTotal > stock) {
    const canAdd = Math.max(stock - currentQty, 0)
    alert(canAdd > 0
      ? ` ${canAdd}  ${stock}`
      : ` ${stock}`)
    return
  }



  // 
  if (existing) existing.quantity += addQty
  else this.items.push({ id, name, price, image, quantity: requestedTotal })



  //  /api/cart/add 
  const auth = useAuthStore()
  if (auth.user?.id) {
    const res = await fetch(`${API_URL}/api/cart/add`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        user_id: auth.user.id,      // 
        product_id: id,             //  schema
        add_quantity: addQty
      })
    })
    // 
    if (!res.ok) {
      const data = await res.json().catch(() => ({}))
      // 
      if (existing) existing.quantity -= addQty
      else this.items = this.items.filter(p => p.id !== id)
      if (data.error === 'INSUFFICIENT_STOCK') {
        alert(` ${data.availableToAdd}  ${data.currentInCart}  ${data.stock}`)
      } else {
        alert(data.error || '')
      }
    }
  }
},


// 
  async updateQuantity(id, quantity) {
    const item = this.items.find(p => p.id === id)
    if (item) item.quantity = quantity

    const auth = useAuthStore()
    if (auth.user?.id) {
      await fetch(`${API_URL}/api/cart`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          user_id: auth.user.id,
          product_id: id,
          quantity
        })
      })
    }
  },
 // 
  async removeItem(id) {
    this.items = this.items.filter(p => p.id !== id)

    const auth = useAuthStore()
    if (auth.user?.id) {
      await fetch(`${API_URL}/api/cart`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          user_id: auth.user.id,
          product_id: id,
          quantity: 0
        })
      })
    }},

  // 
  async loadFromServer(user_id) {
    try {
      const res = await fetch(`${API_URL}/api/cart?user_id=${user_id}`)
      const cartData = await res.json()

    const productRes = await fetch(`${API_URL}/api/products`)
    const products = await productRes.json()

    this.items = Array.isArray(cartData.items)
      ? cartData.items.map(i => {
          const product = products.find(p => p.product_id === i.product_id)
          return {
            id: i.product_id,
            name: product?.name || '',
            price: product?.price || 0,
            image: product?.image || '',
            quantity: i.quantity
          }
        })
      : []
    } catch (err) {
      console.error(' ', err)
      this.items = []
    }
  }},

  getters: {
    // 
    totalPrice: (state) =>
      state.items.reduce((sum, item) => sum + item.price * item.quantity, 0),

    totalQuantity: (state) =>
    state.items.reduce((sum, item) => sum + item.quantity, 0)
  },
  persist: true //  
})
