<template>
  <form @submit.prevent="handleRegister" class="w-full max-w-md mx-auto bg-white p-6 rounded-xl shadow space-y-4">
    <header class="header_area">
      <div class="main_menu">
        <nav class="navbar navbar-expand-lg navbar-light">
          <div class="container">
            <router-link to="/" class="navbar-brand logo_h"><img src="/aroma/img/logo.png" alt=""></router-link>
            <div class="collapse navbar-collapse offset" id="navbarSupportedContent">
              <ul class="nav navbar-nav menu_nav ml-auto mr-auto">
                <h4 class="container mt-2"></h4>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </header>

    <div>
      <label class="block text-sm font-medium"></label>
      <input
        v-model="name"
        type="text"
        class="w-full mt-1 px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        required
      />
    </div>

    <div>
      <label class="block text-sm font-medium"></label>
      <input
        v-model="email"
        type="email"
        class="w-full mt-1 px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        required
      />
    </div>

    <!-- <div>
      <label class="block text-sm font-medium"></label>
      <input
        v-model="password"
        type="password"
        class="w-full mt-1 px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        required
      />
    </div>

    <div>
      <label class="block text-sm font-medium"></label>
      <input
        v-model="confirmPassword"
        type="password"
        class="w-full mt-1 px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        required
      />
    </div> -->

    <div>
      <label class="block text-base font-medium leading-[2.5rem]"></label>
      <div class="relative">
        <input
          :type="showPassword ? 'text' : 'password'"
          v-model="password"
          class="w-full px-4 py-2 pr-10 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
          required
        />
        <span
          @click="togglePasswordVisibility"
          class="absolute inset-y-0 right-3 flex items-center cursor-pointer text-gray-500 text-base"
          title="/"
        >
          {{ showPassword ? '' : '' }}
        </span>
      </div>
    </div>

    <div>
      <label class="block text-base font-medium leading-[2.5rem]"></label>
      <div class="relative">
        <input
          :type="showPassword ? 'text' : 'password'"
          v-model="confirmPassword"
          class="w-full px-4 py-2 pr-10 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
          required
        />
        <span
          @click="togglePasswordVisibility"
          class="absolute inset-y-0 right-3 flex items-center cursor-pointer text-gray-500 text-base"
          title="/"
        >
          {{ showPassword ? '' : '' }}
        </span>
      </div>
    </div>

    <div v-if="errorMessage" class="text-red-500 text-sm">{{ errorMessage }}</div>

    <button
      type="submit"
      class="nav-item"
    >
      
    </button>

    <p class="text-center text-sm text-gray-500">
      
      <router-link to="/login" class="text-blue-500 hover:underline"></router-link>
    </p>
    <p class="text-center text-sm text-gray-500">
      <router-link to="/" class="text-blue-500 hover:underline"></router-link>
    </p>

    <footer class="footer mt-5">
  <div class="footer-area">
    <div class="container text-center pt-5">
      <p class="col-lg-12 footer-text text-center">
  Copyright  {{ new Date().getFullYear() }} All rights reserved |
  This template is made with <i class="fa fa-heart" aria-hidden="true"></i>
  by <a href="https://colorlib.com" target="_blank" rel="noopener">Colorlib</a>
</p>
    </div>
  </div>
</footer>

  </form>
</template>

<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'

const name = ref('')
const email = ref('')
const password = ref('')
const confirmPassword = ref('')
const errorMessage = ref('')
const router = useRouter()

const showPassword = ref(false)
const togglePasswordVisibility = () => {
  showPassword.value = !showPassword.value
}

const handleRegister = async () => {
  if (!name.value || !email.value || !password.value || !confirmPassword.value) {
    errorMessage.value = ''
    return
  }

  if (password.value !== confirmPassword.value) {
    errorMessage.value = ''
    return
  }

  try {
    const API_URL = process.env.VUE_APP_API
    const res = await fetch(`${API_URL}/api/register`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        name: name.value,
        email: email.value,
        password: password.value
      })
    })
    const data = await res.json()

    if (!res.ok) throw new Error(data.message || '')

    // 
    alert('') 
    router.push('/login')
  } catch (err) {
    errorMessage.value = err.message
  }
}
</script>
