<template>
  <div>
    <a class="navbar-brand logo_h"><router-link to="/"><img src="/aroma/img/logo.png" alt=""></router-link></a>
    <button class="btn btn-danger mb-3" @click="handleLogout"></button>
    <h4></h4>
    <table class="table table-bordered">
      <thead>
        <tr>
          <th>ID</th>
          <th></th>
          <th>Email</th>
          <th></th>
          <!-- <th></th> -->
        </tr>
      </thead>
      <tbody>
        <tr v-if="token && user">
          <td>{{ user.id }}</td>
          <td>{{ user.name }}</td>
          <td>{{ user.email }}</td>
          <td>{{ user.registered }}</td>
          <!-- <td><img src="aroma/img/r1.jpg" alt="avatar" style="height: 50px;"></td> -->
        </tr>
        <tr v-else>
          <td colspan="5"></td>
        </tr>
      </tbody>
    </table>
  </div>
  <h4></h4>
  <table class="table table-bordered" v-if="orders.length">
    <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
         <!-- anchor:thead-action  -->
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="order in orders" :key="order.order_id">
        <td>{{ order.order_id }}</td>
        <td>${{ order.amount }}</td>
        <td>{{ order.status }}</td>
        <td>{{ new Date(order.created_at).toLocaleString() }}</td>
        <td>
          <ul>
            <li v-for="item in order.items" :key="item.product_id">
              {{ item.product_id }}  {{ item.quantity }}: ${{ item.price }}
            </li>
          </ul>
        </td>
        <!-- anchor:tbody-action  -->
        <td>
          <button
            v-if="order.status === ''"      
            class="btn btn-success btn-sm"
            :disabled="payingOrderId === order.order_id"
            @click="handlePay(order)"
          >
            {{ payingOrderId === order.order_id ? '' : '' }}
          </button>
          <span v-else></span>
        </td>
      </tr>
    </tbody>
  </table>
  <p v-else></p>
</template>


<script setup>
import { computed, onMounted, ref } from 'vue'
import { useAuthStore } from '@/stores/auth'
import { useCartStore } from '@/stores/cart'
import { useRouter } from 'vue-router'
// anchor:import-axios
import axios from 'axios'
const auth = useAuthStore()
const cart = useCartStore()
const router = useRouter()
const orders = ref([])
// anchor:paying-order-id
const payingOrderId = ref(null) // 

const handleLogout = () => {
  auth.logout()
  cart.clearCart()
  router.push('/')
}

const user = computed(() => auth.user)
const token = computed(() => auth.token)
const API_URL = process.env.VUE_APP_API
onMounted(async () => {
  if (auth.user?.id) {
    const res = await fetch(`${API_URL}/api/orders/${auth.user.id}`)
    orders.value = await res.json()
  }
  })
  // anchor:handle-pay-method
async function handlePay(order) {
  try {
    if (!auth.user?.id) {
      alert(' ')
      return
    }
    if (order.status !== '') { 
      alert('')
      return
    }

    payingOrderId.value = order.order_id

    const ecpayRes = await axios.post(
      `${API_URL}/api/ecpay-pay`,
      {
        orderNo: order.order_id,
        amount: order.amount,
        desc: ` ${order.order_id} `,
        itemName: ` ${order.order_id}`
      },
      {
        headers: token.value ? { Authorization: `Bearer ${token.value}` } : {}
      }
    )

    const formDiv = document.createElement('div')
    formDiv.style.position = 'fixed'
    formDiv.style.left = '-9999px'
    formDiv.innerHTML = ecpayRes.data
    document.body.appendChild(formDiv)

    const form = formDiv.querySelector('form')
    if (!form) throw new Error('')
    form.submit()
  } catch (err) {
    console.error(err)
    alert(' ')
  } finally {
    payingOrderId.value = null
  }
}

</script>
